import React, { useState, useRef, FormEvent } from "react";
import Header from "../../../components/Header";
import Sidebar from "../../../components/Sidebar";
import Input from "../../../utils/Input";
import Footer from "../../../components/Footer";
import firebase, { db } from "../../../config/Firebase";
import Icon from "../../../utils/Icon";
import { Link, useHistory } from "react-router-dom";

const Skill = () => {
  const history = useHistory();
  const [name, setName] = useState("");
  const [color, setColor] = useState("");
  const [percentage, setPercentage] = useState(0);
  const form = useRef<HTMLDivElement>(document.createElement("div"));
  const handleSubmit = (e: FormEvent) => {
    e.preventDefault();
    if (percentage <= 100) {
      if (form.current) {
        form.current.classList.add("show_on_success");
        db.collection("skills")
          .add({ name, color, percentage })
          .then(() => {
            form.current.classList.remove("show_on_success");
            setName("");
            setColor("");
            setPercentage(0);
          })
          .catch((error) => {
            form.current.classList.remove("show_on_success");
          });
      }
    } else {
    }
  };
  const handleClick = () => {
    firebase
      .auth()
      .signOut()
      .then(() => history.push("/"))
      .catch((error) => {});
  };
  return (
    <div className="add_panel">
      <Header admin={true} />
      <Sidebar admin={true} />
      <div className="outer_container">
        <form onSubmit={handleSubmit} className="adding_form container">
          <div className="loader_success" ref={form}>
            <Icon loader={true} />
          </div>
          <h1 id="litheader" className="light_heading">
            ADD NEW SKILL
          </h1>
          <div className="row">
            <Input
              onChange={(e) => setName(e.target.value)}
              value={name}
              type="text"
              id="name"
              placeholder="Name"
            />
            <Input
              onChange={(e) => setColor(e.target.value)}
              value={color}
              type="text"
              id="color"
              placeholder="Color"
            />
          </div>
          <div className="row">
            <Input
              onChange={(e) => setPercentage(+e.target.value)}
              value={percentage}
              type="number"
              id="percentage"
              placeholder="percentage"
            />
          </div>
          <div className="center btn_wrapper">
            <button className="btn_animate browser-default">
              <span></span>
              <span></span>
              <span></span>
              <span></span>
              Add new project
              <span className="shift">›</span>
            </button>
            <Link to="/" className="btn_animate browser-default">
              <span></span>
              <span></span>
              <span></span>
              <span></span>
              Home
            </Link>
            <div
              className="btn_animate"
              style={{ display: `inline-block` }}
              onClick={handleClick}
            >
              <span></span>
              <span></span>
              <span></span>
              <span></span>
              Logout &nbsp;
              <span className="shift">›</span>
            </div>
          </div>
        </form>
        <Footer />
      </div>
    </div>
  );
};

export default Skill;
